@import url(https://fonts.googleapis.com/css?family=Roboto+Slab&display=swap);
* {
	margin: 0;
	box-sizing: border-box;
}

.app {
	text-align: center;
}

.charada {
	display: flex;
	margin: 3px 2%;
	background-color: white;
	border-radius: 5px;
	border: 2px solid black;
	box-shadow: -15px 16px 4px -9px rgba(135, 135, 135, 0.36);
}

.charada-votos {
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	margin: 5px;
}

.charada-votos span {
	margin: auto;
	font-size: 1.8rem;
	border-radius: 50%;
}

.charada-votos button {
	border: none;
	background: none;
}

.charada-votos button:hover {
	cursor: pointer;
}

.charada-votos .positivo {
	color: lawngreen;
}

.charada-votos .negativo {
	color: red;
}

.charada-charada {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	margin: auto;
	width: 75%;
}

.charada-charada button {
	background-color: rgb(0, 0, 0);
	border: none;
	color: white;
	padding: 5px 15px;
	border-radius: 3px;
	margin-bottom: 2px;
}

.charada-charada button:hover {
	cursor: pointer;
}

.charada-votos-numero {
	margin: auto 10px;
	margin-left: auto;
}

@-webkit-keyframes wobble-skew-on-hover {
	16.65% {
		-webkit-transform: skew(-12deg);
		transform: skew(-12deg);
	}
	33.3% {
		-webkit-transform: skew(10deg);
		transform: skew(10deg);
	}
	49.95% {
		-webkit-transform: skew(-6deg);
		transform: skew(-6deg);
	}
	66.6% {
		-webkit-transform: skew(4deg);
		transform: skew(4deg);
	}
	83.25% {
		-webkit-transform: skew(-2deg);
		transform: skew(-2deg);
	}
	100% {
		-webkit-transform: skew(0);
		transform: skew(0);
	}
}
@keyframes wobble-skew-on-hover {
	16.65% {
		-webkit-transform: skew(-12deg);
		transform: skew(-12deg);
	}
	33.3% {
		-webkit-transform: skew(10deg);
		transform: skew(10deg);
	}
	49.95% {
		-webkit-transform: skew(-6deg);
		transform: skew(-6deg);
	}
	66.6% {
		-webkit-transform: skew(4deg);
		transform: skew(4deg);
	}
	83.25% {
		-webkit-transform: skew(-2deg);
		transform: skew(-2deg);
	}
	100% {
		-webkit-transform: skew(0);
		transform: skew(0);
	}
}
.wobble-skew-on-hover {
	display: inline-block;
	vertical-align: middle;
	-webkit-transform: perspective(1px) translateZ(0);
	transform: perspective(1px) translateZ(0);
	box-shadow: 0 0 1px rgba(0, 0, 0, 0);
}
.wobble-skew-on-hover:hover,
.wobble-skew-on-hover:focus,
.wobble-skew-on-hover:active {
	-webkit-animation-name: wobble-skew-on-hover;
	animation-name: wobble-skew-on-hover;
	-webkit-animation-duration: 1s;
	animation-duration: 1s;
	-webkit-animation-timing-function: ease-in-out;
	animation-timing-function: ease-in-out;
	-webkit-animation-iteration-count: 1;
	animation-iteration-count: 1;
}

* {
	margin: 0;
}

body {
	background-color: dodgerblue;
	font-family: 'Roboto Slab', serif;
}

.topcharadas {
	margin: 10px;
	height: 95vh;
	overflow: hidden;
}

.titulo {
	margin: 5px auto;
	margin-top: -5px;
	color: white;
	font-size: 1.4rem;
	width: 50%;
	padding: 15px;
	border-radius: 15px;
	background-color: none;
}

.titulo button {
	border: none;
	background-color: crimson;
	color: white;
	font-size: 1.3rem;
	padding: 10px 20px;
	border-radius: 10px;
}

.titulo button:hover {
	background-color: rgb(253, 44, 85);
	cursor: pointer;
}

.lista-charadas {
	height: 70vh;
	width: 70vw;
	margin: auto;
	overflow-y: scroll;
	background-color: white;
	border: 1px solid white;
	border-radius: 10px;
	padding: 5px;
	box-shadow: -15px 16px 4px -9px rgba(0, 0, 0, 0.36);
}

::-webkit-scrollbar {
	width: 10px;
	height: 10px;
}
::-webkit-scrollbar-button {
	width: 0px;
	height: 0px;
}
::-webkit-scrollbar-thumb {
	background: #e1e1e1;
	border: 0px none #ffffff;
	border-radius: 50px;
}
::-webkit-scrollbar-thumb:hover {
	background: #ffffff;
}
::-webkit-scrollbar-thumb:active {
	background: #888888;
}
::-webkit-scrollbar-track {
	background: #9a9a9a;
	border: 0px none #ffffff;
	border-radius: 100px;
}
::-webkit-scrollbar-track:hover {
	background: #666666;
}
::-webkit-scrollbar-track:active {
	background: #333333;
}
::-webkit-scrollbar-corner {
	background: transparent;
}

@media only screen and (max-width: 600px) {
	.titulo {
		font-size: 0.8rem;
		width: 90%;
	}

	.titulo h1 {
		margin-bottom: 5px;
	}

	.titulo button {
		font-size: 1rem;
	}
}

